@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700,800);
html, 
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    background-color: rgb(245, 245, 245);
}
.container{
  margin: 80px auto 0 auto;
  max-width: 1200px;
}
.nav-container{
  margin: auto;

}
.nav-container svg{
  color: #fff;
}
a{
  text-decoration: none;
}
*{box-sizing:border-box;margin:0}html,body{margin:0;background:rgba(255,255,255,.1);font-family:"Montserrat",helvetica,arial,sans-serif;font-size:14px;font-weight:400}.movie_card{position:relative;display:block;width:800px;height:350px;margin:100px auto;border-radius:10px;transition:all .4s}.movie_card:hover{-webkit-transform:scale(1.02);transform:scale(1.02);transition:all .4s}.movie_card .info_section{position:relative;width:100%;height:100%;background-blend-mode:multiply;z-index:2;border-radius:10px}.movie_card .info_section .movie_header{position:relative;padding:25px;height:40%}.movie_card .info_section .movie_header h1{color:#fff;font-weight:400}.movie_card .info_section .movie_header h4{color:#9ac7fa;font-weight:400}.movie_card .info_section .movie_header .minutes{display:inline-block;margin-top:10px;color:#fff;padding:5px;border-radius:5px;border:1px solid rgba(255,255,255,.13)}.movie_card .info_section .movie_header .type{display:inline-block;color:#cee4fd;margin-left:10px}.movie_card .info_section .movie_header .locandina{position:relative;float:left;margin-right:20px;height:120px;box-shadow:0 0 20px -10px rgba(0,0,0,.5)}.movie_card .info_section .movie_desc{padding:25px;height:30%;width:80%}.movie_card .info_section .movie_desc .text{color:#cfd6e1;white-space:normal}.movie_card .info_section .movie_social{height:10%;padding-top:40px;padding-left:15px}.movie_card .info_section .movie_social ul{list-style:none;padding:0}.movie_card .info_section .movie_social ul li{display:inline-block;color:rgba(255,255,255,.4);transition:color .3s;transition-delay:.15s;margin:0 10px}.movie_card .info_section .movie_social ul li:hover{transition:color .3s;color:rgba(255,255,255,.8)}.movie_card .info_section .movie_social ul li i{font-size:19px;cursor:pointer}.movie_card .blur_back{position:absolute;top:0;z-index:1;height:100%;right:0;background-size:cover;border-radius:11px}@media screen and (min-width: 768px){.movie_header{width:60%}.movie_desc{width:50%}.info_section{background:linear-gradient(to right, #0d0d0c 50%, transparent 100%)}.blur_back{width:80%;background-position:-100% 10% !important}}@media screen and (max-width: 768px){.movie_card{width:95%;margin:70px auto;min-height:350px;height:auto}.blur_back{width:100%;background-position:50% 50% !important}.movie_header{width:100%;margin-top:85px}.movie_desc{width:100%}.info_section{background:linear-gradient(to top, #141413 50%, transparent 100%);display:inline-grid}}#bright{box-shadow:0px 0px 150px -45px rgba(255,51,0,.5)}#bright:hover{box-shadow:0px 0px 120px -55px rgba(255,51,0,.5)}
